      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
  },
  mounted() {
    if (this.activePatient.name == this.patient.name) {
      this.activeStatus = "secondary";
    } else {
      this.activeStatus = "";
    }
  },
